<template>
  <div id="myZone">
    <head-top title="个人中心" :show-back="false"></head-top>
    <div class="myzoneFaceWwall">
      <div class="myFace">
        <div class="myFaceImg">
          <van-uploader capture="camera" preview-image :before-read="beforeRead" :after-read="afterRead">
            <img :src="userPhoto" alt=""/>
          </van-uploader>
        </div>
        <div class="MyFaceName">{{ userName }}</div>
        <div class="maFaceFt"></div>
      </div>
    </div>
    <div class="myZoneContent">
      <div class="myZoneContentItem" v-for="item in Details" :key="item.info" @click="toPath(item.path)">
        <span class="info">{{ item.info }}</span>
        <i class="iconfont fL" :class="item.icon"></i>
        <i class="iconfont icon-qianjin fR"></i>
      </div>
    </div>
    <div class="myZoneFooter">
      <van-button type="primary" :loading="loading" loading-text="退出登录中..." @click="toLogout">
        退出登录
      </van-button>
    </div>
  </div>
</template>

<script>
import imgDemo from "./../../assets/boy_img.png";

export default {
  name: "",
  props: {},
  data () {
    return {
      userName: (this.$store.getters.teacher && this.$store.getters.teacher.TeacherName) || "",
      userPhoto: imgDemo,
      loading: false,
      Details: [
        { path: "/personal-data", info: "个人资料", icon: "icon-gerenziliao" },
        { path: "/help-use", info: "使用说明", icon: "icon-word" },
        { path: "/about-us", info: "关于我们", icon: "icon-guanyuwomen" }
      ]
    };
  },
  methods: {
    // 返回布尔值
    beforeRead (file) {
      if (
        file.type !== "image/jpeg" &&
        file.type !== "image/png" &&
        file.type !== "image/jpg"
      ) {
        this.$toast("请上传 jpg 格式图片");
        return false;
      }
      // 超过20M
      if (file.size > 1024 * 1024 * 20) {
        this.$toast("请上传小于20M的图片");
        return false;
      }
      return true;
    },
    afterRead (file) {
      // console.log("file:", file);
      if (file) {
        // this.$gatewayHttp.resourse.upload({funType: 1, imgBase64: file["content"]}).then(res => {
        //     if (res.Result) {
        //         this.userPhoto = file["content"];
        //     }
        // }).catch(err => {
        // });
      }
    },
    toLogout () {
      // console.log("退出登录");
      this.loading = true;
      this.$store
        .dispatch("Logout")
        .then(res => {
          this.loading = false;
        }).catch(() => {
        this.loading = false;
      });
      this.$router.replace("/login");
    },
    toPath (path) {
      this.$router.push(path);
    }
  }
};
</script>

<style lang="less">
@import "./../../styles/common/base";
#myZone {
  position: relative;
  width: 100%;
  height: 100%;

  .myzoneFaceWwall {
    width: 100%;
    height: 160px;
    margin-bottom: 10px;
    background-image: url("../../assets/pesonbg.png");
    background-repeat: no-repeat;
    background-size: 100vw 136px;
  }

  .myFace {
    width: 73vw;
    height: 110px;
    background: rgba(255, 255, 255, 1);
    position: absolute;
    left: 0;
    right: 0;
    top: 44px;
    margin: 0 auto;
    @img-width: 45px;

    .myFaceImg {
      margin: 1vw auto 0;

      img {
        max-width: @img-width;
        max-height: @img-width;
        border-radius: 50%;
        border: 1px solid #00cbab;
      }
    }

    .MyFaceName {
      margin: 0.4vw auto 0;
      height: 16px;
      font-size: @font-title;
      font-family: MicrosoftYaHei;
      color: rgba(51, 51, 51, 1);
      line-height: 1.2;
    }

    .maFaceFt {
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      height: 30px;
      background-image: url("../../assets/facebg.png");
      background-size: 100% 30px;
      background-repeat: no-repeat;
    }
  }

  .myZoneContent {
    width: 100vw;
    padding-bottom: 12px;
    background: rgba(255, 255, 255, 1);

    .myZoneContentItem {
      width: 94vw;
      height: 50px;
      border-bottom: 1px solid rgba(221, 221, 221, 1);
      line-height: 50px;
      text-align: left;
      margin: 0 auto;

      .info {
        display: inline-block;
        font-size: @font-info;
        font-family: MicrosoftYaHei;
        font-weight: 400;
        color: rgba(51, 51, 51, 1);
        margin-left: 4vw;
      }

      .icon-gerenziliao {
        font-size: @font-sub-title;
        color: #21b1f4;
      }

      .icon-word {
        font-size: @font-sub-title;
        color: #ff976a;
      }

      .icon-tongji {
        font-size: @font-sub-title;
        color: #fe6b6b;
      }

      .icon-guanyuwomen {
        font-size: @font-sub-title;
        color: #00cbab;
      }
    }
  }

  .myZoneFooter {
    position: absolute;
    bottom: 70px;
    left: 18vw;

    .van-button--primary {
      width: 64vw;
      background: rgba(0, 203, 171, 1);
      border-radius: 5px;
      border: 1px solid #00cbab;
    }
  }
}
</style>
